  filterDoctorAppointment(param) {
    this.filterAppointmentList.next(param)
  }


  doctorDetail(param) {
    this.doctorDetails.next(param);
  }

  //get urlpath for generating active class for navbar
  setPath(param) {
    this.urlPathInfo.next(param);
  }


